.package-wall {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 320px;
  min-height: 30vh;
  position: relative;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-align-items: stretch;
      -ms-flex-align: stretch;
          align-items: stretch; }
  .package-wall:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
    background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.4)); }
  @media only screen and (min-width: 40.063em) {
    .package-wall {
      background-attachment: fixed; } }

.package-wall-content {
  color: #FFFFFF;
  position: relative;
  z-index: 1;
  /* IE hack - cannot work without explicit height */
  min-height: 320px;
  min-height: 30vh;
  text-shadow: 0 0 24px rgba(0, 0, 0, 0.8);
  padding: 0 2%;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%; }

.package-tagline {
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 2px;
  word-spacing: 1rem; }

.icon-big {
  width: 1.5rem;
  height: 1.5rem; }

.panel-heading {
  margin-top: 2rem;
  margin-bottom: 0; }

.quick-info-panel {
  padding: 0 1rem; }
  .quick-info-panel .package-title {
    margin: 0;
    margin-top: 1rem; }
  .quick-info-panel .quick-info-item {
    margin-top: 4px;
    margin-right: 1rem; }
  .quick-info-panel .stay-item {
    background-color: rgba(148, 192, 68, 0.5);
    border: 1px solid #94C044;
    padding: 0.25rem 0.5rem;
    margin-right: 0.5rem;
    margin-top: 0.5rem; }

.itinerary-panel {
  padding: 0 1rem; }
  .itinerary-panel .itinerary-content {
    -webkit-column-count: 3;
       -moz-column-count: 3;
            column-count: 3;
    -webkit-column-width: 300px;
       -moz-column-width: 300px;
            column-width: 300px;
    -webkit-column-gap: 3rem;
       -moz-column-gap: 3rem;
            column-gap: 3rem;
    -webkit-column-rule: 1px dotted #91D8F6;
       -moz-column-rule: 1px dotted #91D8F6;
            column-rule: 1px dotted #91D8F6; }
  .itinerary-panel .day-heading {
    color: #82b02d; }

/* Used in upcoming packages */
.departure-date-content h4 {
  line-height: 1;
  margin: 0; }

.departure-date-content .cost-item:nth-child(n+2) h4 {
  margin-top: 1rem; }

.departure-date-content .tour-cost {
  font-size: 2rem;
  color: #94C044;
  display: inline-block; }

.departure-date-content .rupee-symbol {
  fill: #94C044;
  vertical-align: baseline;
  width: 1.25rem;
  height: 1.25rem; }

.departure-date-content .service-tax-text {
  font-size: 75%;
  font-weight: bold; }
